html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

ol[class],
ul[class] {
  list-style: none;
}

a {
  text-decoration: none;
}

button,
a {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

button,
input,
select,
textarea {
  color: inherit;
  border: 0;
}

.btn {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 16px;
  align-items: center;
  background-color: #363838;
  color: #fffff0;
  border-radius: 21px;
  border: 1px #363838 solid;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn:hover {
  background-color: #fffff0;
  color: #363838;
  color: #363838;
}
.btn:active {
  transform: scale(0.96);
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  letter-spacing: 0px;
  text-align: left;
}

footer {
  font-family: "Montserrat", sans-serif;
  font-weight: 110;
}

body {
  color: red;
}