@use 'var' as *;

.btn {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 16px;
  align-items: center;
  background-color: $bg-btn;
  color: $btn-color;

  border-radius: 21px;
  border: 1px $bg-btn solid;
  transition: background-color, color, 0.5s ease-out;
  user-select: none; // запрет выделение текста внутри элемента 
  &:hover {
    background-color: $btn-color;
    color: $bg-btn;
    color: $bg-btn;
  }
  &:active {
    transform: scale(0.96);
  }
}
